input {
    display: block;
    padding: 2px;
    margin: 5px 0 15px 0;
}

button {
    padding: 5px;
    margin: 5px 0 15px 0;
}
ul {
    padding: 0;
    list-style-type: none;
}

table td {
    padding: 3px 10px 3px 0;
}
.notification {
    color: blue;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}
